/* src/components/About.css */

.section {
    padding: 80px 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.section-title {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 40px;
    color: #4a148c;
}

.about-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-img {
    flex: 1;
    margin-right: 40px;
}

.about-img img {
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.about-text {
    flex: 2;
    color: #333333;
    line-height: 1.6;
}

@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
    }

    .about-img {
        margin-right: 0;
        margin-bottom: 20px;
    }
}
